@keyframes glow {
  0% {
    box-shadow: 0 0 0 0 #dc143c;
  }
  50% {
    box-shadow: 0 0 30px 0 #dc143c;
  }
}
.btn {
  background: none;
  color: inherit;
  font: inherit;
  border: none;
  outline: none;
  cursor: pointer;
}

.btn-glow {
  background: #dc143c;
  color: #fff;
  font-size: 1.6em;
  font-weight: 600;
  text-transform: uppercase;
  padding: 16px 34px;
  box-shadow: 0 0 0 0 #dc143c;
  -webkit-animation: glow 1.4s linear infinite;
  animation: glow 1.4s linear infinite;
}