// Please change your favorite font-size!
$fontSize: 15vmin;
div {
  will-change: transform;
}

.background {
  height: 30%;
  .text {
    pointer-events: none;
    position: absolute;
    width: 100%;
    height: 100%;
    font-size: $fontSize;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    opacity: 0;
    line-height: $fontSize;
    font-family: 'Anton', sans-serif;
    transform: translate(0, 0);

    @for $i from 0 through 40 {
      $key: $i + 1;
      &:nth-child(#{$key}) {
        $row: floor($i / 20);
        color: rgba(random(200) + 55, random(200) + 55, random(200) + 55, 1);
        clip-path: polygon(
                        $i * 5% - $row * 100% - random(2) $row * 50% - random(10),
                        $key * 5% - $row * 100% + random(5) $row * 50% - random(25),
                        $key * 5% - $row * 100% + random(5) $row * 50% + 50% + random(10),
                        $i * 5% - $row * 100% - random(2) $row * 50% + 50% + random(25)
        );

        transform-origin: random(100) - 50 + floor($i / 2) * 10% - $row * 100% random(100) - 50 + $row * 50%;
        animation: fly#{$key} 5000ms $i * 30ms cubic-bezier(0.360, 0.100, 0.160, 1.000) 1 normal;
        animation-fill-mode: forwards;

        @keyframes fly#{$key} {
          $initX: random(1000) - 500;
          $initY: random(1000) - 500;
          $initZ: random(1000) - 500;
          $initDepth: random(3000) - 2500;
          0% {
            opacity: 0;
            transform: translate(0, 0) rotateX(#{$initX}deg) rotateY(#{$initY}deg) rotateZ(#{$initZ}deg) translateZ(#{$initDepth}px);
          }
          10% {
            opacity: 0;
            transform: translate(0, 0) rotateX(#{$initX}deg) rotateY(#{$initY}deg) rotateZ(#{$initZ}deg) translateZ(#{$initDepth}px);
          }
          90% {
            opacity: 1;
            transform: translate(0, 0) rotate(0deg) rotateY(0deg) rotateZ(0deg) translateZ(0px);
          }
          100% {
            opacity: 1;
            transform: translate(0, 0) rotate(0deg) rotateY(0deg) rotateZ(0deg) translateZ(0px);
          }
        }
      }
    }
  }
}
.background {
  @media screen and (max-width: 340px) {
    height: 10%;
  }
}
