@font-face {
  font-family: "Author Font";
  src: url('./12205.ttf') format("truetype");
}

* {
  margin: 0;
  padding: 0;
}

body {
  background: #0A122A;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  perspective: 800px;
}

.App {
  text-align: center;
  width: 100%;
  height: 100%;

}

#root {
  width: 100%;
  height: 100%;
}
.buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  animation-name: show;
  animation-delay: 5000ms;
  animation-duration: 200ms;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: forwards;
}
.author {
  position: absolute;
  margin: 1rem;
  font-size: 2rem;
  right: 0;
  bottom: 0;
  margin-top: 3rem;
  text-align: right;
  font-family: "Author Font", sans-serif;

}

@keyframes show {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}

@media screen and (max-width: 370px) {
  .App {
    overflow-y: auto;
  }
  .author {
    position: relative;
  }
}

